.Home{display:flex;flex-direction:column;text-align:center;position:relative}.Home .Home__title h1{font-size:10vw;line-height:.9}.Home .Home__subtitle{display:flex;justify-content:center;margin:60px}.Home .Home__text{text-align:justify}
.Work .Work_filter{margin:100px 0 50px;display:flex;grid-gap:15px;gap:15px;align-items:center}.Work .Work_filter button{background-color:rgba(0,0,0,0);background-color:#222;border:1px solid #222;border-radius:10px;color:#efece0;padding:10px 20px;cursor:pointer;transition:.25s;font-size:calc(16px + (20 - 16) * ((100vw - 300px) / (1600 - 300)))}.Work .Work_filter button:hover,.Work .Work_filter .Work_filter_active{background-color:#efece0;color:#222}.Work .Work_list .Work_grid_item a{text-decoration:none}.Work .Work_list .Work_grid_item h2{font-size:4vw;color:#efece0}.Work .Work_list .Work_grid_item h2:hover{color:#0069a6}.Work .Work_list .Work_grid_item .Work_grid_item_filter{color:#222}.Work .Work_list .Work_grid_item img{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);height:50vh;z-index:-1}
.Bio h1{text-align:center}.Bio .ScrollToTop__Bio{position:fixed;bottom:0;right:0;padding:25px}.Bio .Bio__description{margin:50px 0}.Bio .Bio__section:nth-child(odd){background-color:#222}.Bio .Bio__section{max-width:calc(800px + (1200 - 800) * ((100vw - 300px) / (1600 - 300)));margin:0 auto;text-align:center;display:grid;grid-template-columns:1fr;padding:20px}.Bio .Bio__section h2{text-align:center;margin-bottom:10px}.Bio .Bio__item{margin-bottom:25px}.Bio .Bio__item .Bio__item__inner{font-style:italic}.Bio .Bio__item .Bio__item__inner .Bio__role{font-weight:700}.Bio .Bio__item:last-child{margin-bottom:0}@media only screen and (min-width: 700px){.Bio .Bio__description{text-align:start}.Bio .Bio__section{display:grid;grid-template-columns:1fr 3fr;text-align:start}.Bio .Bio__section h2{text-align:start;margin-bottom:0}.Bio .Bio__item__inner{margin-left:25px}}
.Error404 h1{text-align:center}.Error404 p{text-align:center;margin-top:60px}
.workpage{display:grid;grid-template-columns:2fr 1fr;grid-gap:25px;gap:25px;position:relative}.workpage .workpage__images{display:flex;flex-direction:column;align-items:center}.workpage .workpage__images .workpage__images__item{margin:25px}.workpage .workpage__images .workpage__images__item p{text-align:center}.workpage .workpage__images .workpage__images__item img{max-width:100%;background-color:#fff}.workpage .workpage__text .workpage__text__metadata{margin-bottom:15px;font-weight:bold}.workpage .workpage__back{position:fixed;bottom:0;right:0;padding:25px}
.App{margin:calc(60px + (100 - 60) * ((100vw - 300px) / (1600 - 300))) 20px}@media only screen and (min-width: 1000px){.App{margin:calc(60px + (100 - 60) * ((100vw - 300px) / (1600 - 300))) 150px}}
.Contact{position:relative;display:flex;flex-direction:column;align-items:flex-end}.Contact h1{align-self:center}.Contact .Contact__info{margin:150px 0;display:flex;flex-direction:column;text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}
.footer__copyright{background-color:#222;padding:25px;text-align:center}
nav{position:fixed;top:0;z-index:998}nav a{text-decoration:none}nav .nav__header{z-index:999;width:100%;display:flex;align-items:center;justify-content:space-between;padding:20px;position:fixed}nav .nav__overlay{position:fixed;top:0;left:0;right:0;height:0;background-color:#222;overflow:hidden;transition:.5s}nav .nav__overlay .nav__content{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;height:100%;overflow:hidden}nav .nav__overlay .nav__content .nav__main h1{margin-top:30px}nav .nav__overlay .nav__content .nav__footer{position:absolute;bottom:0;padding:50px;display:flex;align-items:center;grid-gap:50px;gap:50px}nav .nav__overlay .nav__content .nav__footer svg path{fill:#efece0}
#nav_icon:hover{cursor:pointer}#nav_icon{width:calc(1.414213 * calc(20px + (40 - 20) * ((100vw - 300px) / (1600 - 300))));height:calc(20px + (40 - 20) * ((100vw - 300px) / (1600 - 300)));transform:rotate(0deg);transition:.5s ease-in-out}#nav_icon span{display:block;position:absolute;height:2px;width:100%;background:#efece0;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav_icon span:nth-child(1){top:0px;transform-origin:left center}#nav_icon span:nth-child(2){top:calc(calc(20px + (40 - 20) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav_icon span:nth-child(3){top:calc(calc(20px + (40 - 20) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav_icon.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav_icon.open span:nth-child(2){width:0%;opacity:0}#nav_icon.open span:nth-child(3){transform:rotate(-45deg);top:calc(20px + (40 - 20) * ((100vw - 300px) / (1600 - 300)));right:0px}
*{padding:0;margin:0;scroll-behavior:smooth;box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box}body{background-color:#1a1a1a;overflow-x:hidden;font-family:"Poppins",sans-serif;color:#efece0;font-size:calc(16px + (20 - 16) * ((100vw - 300px) / (1600 - 300)))}body h1{font-family:"Space Grotesk",sans-serif;font-size:calc(30px + (75 - 30) * ((100vw - 300px) / (1600 - 300)))}body h2{font-family:"Poppins",sans-serif;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}body a{color:#efece0;transition:color .2s}body a:hover{color:#0069a6;cursor:pointer}::selection{background:#0069a6}::-moz-selection{background:#0069a6}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#efece0}
